import React from "react";
import Base from "../core/Base";
import ServiceRequest from "../core/ServiceRequest";
import "../styles.css";
import { Link } from "react-router-dom";
import DashboardHeader from "../core/DashboardHeader";
import { isAuthenticated } from "../auth/helper";
import Card from "react-bootstrap";
import HomeServices from "../core/HomeSevices";
import Footer from "./Footer";

const UserDashboard = () => {
  const {
    user: { name },
  } = isAuthenticated();
  return (
    <div>
      <Base>
        <DashboardHeader title="Welcome " />
        <div className="mt-5 ml-1 mb-5">
          <div className="ex1 card ml-5 mr-5">
            <div className="card-body">
              <h2 className="card-title text-muted display-4">
                Make a Door-Step Service Request
              </h2>
              <h2
                className="card-text text-muted mb-5 mt-4"
                style={{ textDecoration: "underline" }}
              >
                We Care for the Cars !!
              </h2>
              <div className="ex2">
                <Link
                  to="/user/servicerequest"
                  className="nav-link text-white text-center"
                >
                  Request Service
                </Link>
              </div>
            </div>
          </div>
        </div>
      </Base>
      <div className="bg-white ml-5 mr-5 mb-4 px-5 py-5 mt-4">
        <h3 className="display-4 text-center mb-1">Services</h3>
        <hr className="hs mb-5" />
        <HomeServices />
      </div>
      <Footer />
    </div>
  );
};
export default UserDashboard;
