import React, { useState } from "react";
import Base from "../core/Base";
import { Redirect } from "react-router-dom";
import "../styles.css";
import { signin, authenticate, isAuthenticated } from "../auth/helper";
import Footer from "./Footer";

const Signin = () => {
  const [values, setValues] = useState({
    email: "",
    password: "",
    error: "",
    loading: false,
    didRedirect: false, //we will get Redirect from react-router-dom
  });

  const { email, password, error, loading, didRedirect } = values; //destructuring the value

  const { user } = isAuthenticated();

  const handleChange = (name) => (event) => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, error: false, loading: true });
    signin({ email, password })
      .then((data) => {
        if (data.error) {
          setValues({ ...values, error: data.error, loading: false });
        } else {
          authenticate(data, () => {
            //whenever there is next() we can fire up a callback
            setValues({
              ...values,
              didRedirect: true,
            });
          });
        }
      })
      .catch();
  };

  const performRedirect = () => {
    if (didRedirect) {
      if (user && user.role === 1) {
        return <Redirect to="/admin/dashboard" />;
      } else {
        return <Redirect to="/user/dashboard" />;
      }
    }
    if (isAuthenticated()) {
      return <Redirect to="/" />;
    }
  };

  const errorMessage = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <div
            className="alert alert-danger"
            style={{ display: error ? "" : "none" }}
          >
            {error}
          </div>
        </div>
      </div>
    );
  };

  const loadingMessage = () => {
    return (
      loading && (
        <div className="alert alert-info">
          <h2>Loading...</h2>
        </div>
      )
    );
  };

  const signInForm = () => {
    return (
      <div className="row">
        <div className="col-md-8 offset-sm-3 mt-5">
          <div
            className="card text-white bg-dark mt-2"
            style={{ width: "50rem" }}
          >
            <div className="card-body border-dark mt-3 mb-5">
              <div className="card-text">
                <h1 className="mt-3 mb-5">Sign In </h1>
                <p className="display"></p>
              </div>
              <div className="row">
                <div className="col-md-6 offset-sm-3 text-left">
                  <form>
                    <div className="form-group">
                      <label className="text-light">Email</label>
                      <input
                        onChange={handleChange("email")}
                        value={email}
                        className="form-control"
                        type="email"
                      />
                    </div>
                    <div className="form-group mt-3">
                      <label className="text-light">Password</label>
                      <input
                        onChange={handleChange("password")}
                        value={password}
                        className="form-control"
                        type="password"
                      />
                    </div>
                    <button
                      onClick={onSubmit}
                      className="btn btn-secondary btn-block mt-4"
                    >
                      Submit
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  return (
    <Base>
      <div>
        {loadingMessage()}
        {errorMessage()}
        {signInForm()}
        {performRedirect()}
      </div>
      <Footer />
    </Base>
  );
};
export default Signin;
