import React, { useState } from "react";
import { Link } from "react-router-dom";
import { signup } from "../auth/helper";
import { Redirect } from "react-router-dom";

const Signup = () => {
  const [values, setValues] = useState({
    name: "",
    email: "",
    password: "",
    error: "",
    didRedirect: false,
    success: false,
  });
  //will do destructure
  const { name, email, password, error, success, didRedirect } = values;
  const handleChange = (name) => (event) => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };
  const onSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, error: false }); //all values will be brought here when we use'...values'
    signup({ name, email, password })
      .then((data) => {
        if (data.error) {
          console.log(data.error);
          setValues({
            ...values,
            error: data.error,
            success: false,
            didRedirect: false,
          });
        } else {
          setValues({
            ...values,
            name: "",
            email: "",
            password: "",
            error: "",
            success: true,
            didRedirect: true,
          });
        }
      })
      .catch();
  };

  const performRedirect = () => {
    if (didRedirect) {
      return <Redirect to="/user/dashboard" />;
    }
  };
  const signUpForm = () => {
    return (
      <div className="row">
        <div className="col-md-12  text-left">
          <form>
            <div className="form-group">
              <label className="text-light">Name</label>
              <input
                className="form-control"
                onChange={handleChange("name")}
                type="text"
                value={name}
              />
            </div>
            <div className="form-group">
              <label className="text-light">Email</label>
              <input
                className="form-control"
                onChange={handleChange("email")}
                type="email"
                value={email}
              />
            </div>
            <div className="form-group">
              <label className="text-light">Password</label>
              <input
                className="form-control"
                onChange={handleChange("password")}
                type="password"
                value={password}
              />
            </div>
            <button onClick={onSubmit} className="btn btn-secondary btn-block">
              <h4>Submit</h4>
            </button>
          </form>
        </div>
      </div>
    );
  };

  const errorMessage = () => {
    return (
      <div className="row">
        <div className="col-12 text-left text-center ">
          <div
            className="alert alert-danger"
            style={{ display: error ? "" : "none" }}
          >
            {error}
          </div>
        </div>
      </div>
    );
  };
  const successMessage = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <div
            className="alert alert-success"
            style={{ display: success ? "" : "none" }}
          >
            New account was created successfully.Please{" "}
            <Link to="/signin">Login Here</Link>
          </div>
        </div>
      </div>
    );
  };
  return (
    <div>
      {successMessage()}
      {errorMessage()}
      {signUpForm()}
      {performRedirect()}
    </div>
  );
};
export default Signup;
